@import "basic/fonts";
@import "basic/reset";
@import "basic/basic";
.preload {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: #fff;
  &__img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}


body {
  overflow-x: hidden;
  font-family: sans-serif;
  &.lock {
    overflow: hidden;
  }
  font-family: sans-serif;
}

.container {
  display: flex;
  justify-content: center;
  padding: 10px 0 50px;
  width: 100%;
}
.hidden {
  display: none;
}

.canvas {
  margin: 0 auto;
  height: 400px;
  background: url(../img/bg/bg.png) repeat;
  margin-bottom: 0;
  display: block;
  margin-bottom: 10px;
  &__wrapper {}
  &__tools {
    // outline: 1px solid red;
  }
}

input[disabled] {
  opacity: 0.35;
  background-color: #cccccc;
}

label.disabled, label.disabled + input[type="radio"] {
  opacity: .5;
  &:hover {
    opacity: 1;
  }
}

button[name="plus"], button[name="minus"] {
  width: 40px;
  text-align: center;
  padding: 5px 0;
  border-radius: 5px;
}

.tools {
  display: flex;
  align-items: center;
  flex-direction: column;
  &__title {
    font-weight: 700;
    font-size: 20px;
    display: inline-block;
    margin-bottom: 5px;
  }
  &__row {
    margin-bottom: 8px;
  }
  &__column {
    margin-bottom: 8px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  & input {
    // max-width: none;
  }
  & label {
    // display: flex;
    // flex-direction: column;
    // align-items: center;
  }
  &__slider-wrapper {
    // width: 100%;
    // overflow-x: hidden;
  }
  &__slider {
    overflow: hidden;
    background-color: rgb(158, 200, 255);
    width: 100%;
    margin-bottom: 10px;
    & > .swiper-wrapper {
      width: 100vw;
      height: 100%;

    }
  }
  &__slide {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 12px 8px;
    background-color: rgb(158, 200, 255);
  }
  &__text {
    width: 100%;
    color: rgb(0, 0, 0);
    padding: 6px 10px;
    margin-bottom: 10px;
    &::placeholder {
      color: rgba(0, 0, 0, 0.7);
    }
  }
  &__input-number {
    padding: 5px 8px;
    max-width: 100px;
    display: inline-block;
  }
  &__fontfamily {
    padding: 6px 10px;
    width: 100%;
    margin-bottom: 10px;
    & > option {
      padding: 6px 10px;
      display: inline-block;
    }
  }
  &__align-grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 10px;
    margin-bottom: 10px;
    & input {
      flex: 0 0 30%;
      display: block;
      padding: 0;
      margin: 0;
      margin-bottom: 10px;
      height: 30px;
      background-color: rgba(255, 255, 255, 0);
      &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgb(208, 215, 223);
        border-radius: 5px;
        transition: .2s;
      }
      &:checked::after {
        background-color: rgb(105, 163, 240);
      }
    }
  }
  &__save {
    display: block;
    background-color: rgb(158, 200, 255);
    padding: 15px;
    border-radius: 8px;
    &:focus {
      background-color: rgb(128, 177, 240);
    }
    &:active {
      background-color: rgb(111, 155, 212);
    }
    & > img {
      width: 30px;
      height: 30px;
      margin: 0 auto;
    }
  }
  &__textcolor {}
  &__strokewidth {}
  &__strokecolor {}
  &__fontsize {}
}



.panel {
  position: relative;
  z-index: 1;
  &.disabled {
    opacity: .4;
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}

